#toggles {
    display: flex;
    flex-flow: column nowrap;
}

#controls {
    font-family: 'Helvetica Neue', Arial, Helvetica, sans-serif;
    color: #333333;
    font-weight: bold;
    position: fixed;
    top: 1em;
    right: 1em;
    display: flex;
    flex-flow: column nowrap;
    width: 10em;
    z-index: 99;
    height: 100%;
}

#controls h2 {
    margin-bottom: 10px;
    text-align: center;
    background-color: #ffffff;
    border: 2px solid white;
    padding: 4px 8px 4px 8px;
    border-radius: 4px;
    font-size: 1em;
}

#risktoggles {
    display: flex;
    flex-flow: column nowrap;
}
