
.targetcontent {
    background-color: #54ffa4;
    border: 2px solid white;
    padding: 4px 8px 4px 8px;
    margin-left: 10px;
    border-radius: 4px;
}

.c {
    margin-bottom: 10px;
    text-align: center;
}

.nottargetcontent {
    background-color: #c5e0cd;
    border: 2px solid #b4b4b4;
    padding: 4px 8px 4px 8px;
    margin-left: 10px;
    border-radius: 4px;
}

#parent {
    z-index: 99;
    font-family: 'Helvetica Neue', Arial, Helvetica, sans-serif;
    font-weight: bold;
    font-size: 1em;
    color: #333333;
    display: flex;
    flex-flow: column nowrap;
    width: 10em;
}

#parent h2 {
    margin-bottom: 10px;
    text-align: center;
    background-color: #ffffff;
    border: 2px solid white;
    padding: 4px 8px 4px 8px;
    border-radius: 4px;
    font-size: 1em;
}